<template>
	<div class="footer_div">
		<el-row>
		  <el-col :span="6">
		  	<div class="grid-content" @click="searchTab">
		  		<img :src="this.itemCode==1? search_active_img:searchimg" alt="">
		  		<span :class="this.itemCode==1? 'active_span':'menutitle'">搜索</span>
		  	</div>
		  </el-col>
		  <el-col :span="6">
		  	<div class="grid-content" @click="takeoutTab">
		  		<img :src="this.itemCode==2? takeout_active_img:takeoutimg" alt="">
		  		<span :class="this.itemCode==2? 'active_span':'menutitle'">取出</span>
		  	</div>
		  </el-col>
		  <el-col :span="6">
		  	<div class="grid-content" @click="takeinTab">
		  		<img :src="this.itemCode==3? takein_active_img:takeinimg" alt="">
		  		<span :class="this.itemCode==3? 'active_span':'menutitle'">放入</span>
		  	</div>
		  </el-col>
		  <el-col :span="6">
		  	<div class="grid-content" @click="aboutmeTab">
		  		<img :src="this.itemCode==4? aboutme_active_img:aboutmeimg" alt="">
		  		<span :class="this.itemCode==4? 'active_span':'menutitle'">我的</span>
		  	</div>
		  </el-col>
	</el-row>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				itemCode:1,
				searchimg:require("../../../assets/images/search.png"),
				search_active_img:require("../../../assets/images/search_active.png"),
				takeoutimg:require("../../../assets/images/takeout.png"),
				takeout_active_img:require("../../../assets/images/takeout_active.png"),
				takeinimg:require("../../../assets/images/takein.png"),
				takein_active_img:require("../../../assets/images/takein_active.png"),
				aboutmeimg:require("../../../assets/images/aboutme.png"),
				aboutme_active_img:require("../../../assets/images/aboutme_active.png"),
			}
		},
		methods: {
			searchTab(){
				this.itemCode = 1
				this.$router.push('./pdasearch')
			},
			takeoutTab() {
				this.itemCode = 2
				this.$router.push('./pdaout')
			},
			takeinTab() {
				this.itemCode = 3
				this.$router.push('./pdain')
			},
			aboutmeTab() {
				this.itemCode = 4
				this.$router.push('./aboutme')
			}
		}
	}
</script>
<style scoped>
	.footer_div{
		position: fixed;
		bottom: 0;
		height: 1.22rem;
		line-height: 1.12rem;
		width:100%;
		text-align: center;
	}
	.grid-content{
		background: white;
		display: flex;
		height: 1.22rem;
		flex-direction: column;  /* 子元素横向排列 */
    justify-content: center; /* 相对父元素水平居中 */
    align-items: center; /*  子元素相对父元素垂直居中 */
    /*border-right:1px solid white;*/
	}
	.grid-content img{
		width:0.64rem;
		height: 0.64rem;
	}
	.iconhome{
		font-size: 0.7rem;
		line-height: 0.7rem;
		height: 0.7rem;
	}
	.menutitle{
		height: 0.44rem;
		line-height: 0.44rem;
		font-size: 0.36rem;
		color:#8a8a8a;
	}
	.active_span{
		color:#409EFF;
		height: 0.44rem;
		line-height: 0.44rem;
		font-size: 0.36rem;
	}
	a{
		text-decoration: none;
	}
	.router-link-active {
    text-decoration: none;
    background: #fff;
	}
	.el-col-active{
		background: red;
	}
</style>


